import React from 'react';
import { detectAlerts } from '../utils/alerts';

const AlertChip = ({ children, type }) => {
    let bg = type === 'tox' ? 'rgba(239,68,68,0.1)' : type === 'pains' ? 'rgba(245,158,11,0.15)' : 'rgba(249,115,22,0.1)';
    let border = type === 'tox' ? 'rgba(239,68,68,0.3)' : type === 'pains' ? 'rgba(245,158,11,0.4)' : 'rgba(249,115,22,0.3)';
    let color = type === 'tox' ? '#EF4444' : type === 'pains' ? '#D97706' : '#C2410C';
    let icon = type === 'tox' ? '' : type === 'pains' ? '' : '';

    return (
        <div style={{ display: 'inline-flex', alignItems: 'center', gap: 6, background: bg, border: `1px solid ${border}`, color: color, borderRadius: 6, padding: '4px 10px', fontSize: 11, fontWeight: 700 }}>
            <span style={{ fontSize: 10 }}>{icon}</span> {children}
        </div>
    );
};

export default function AdmetPanel({ mol, smiles }) {
    if (!mol) return null;

    const { alerts, cypPredictors, hergPredictors } = detectAlerts(smiles);
    const toxCols = ["Hepatotoxicity", "Mutagenicity", "Carcinogenicity", "Cardiotoxicity", "Nephrotoxicity"];

    // Grab explicit CSV tox flags
    const csvTox = toxCols
        .filter(c => mol[c] == 1 || mol[c] === "1" || mol[c] === true)
        .map(c => c.replace("toxicity", "tox"));

    const noAlerts = csvTox.length === 0 && alerts.length === 0 && cypPredictors.length === 0 && hergPredictors.length === 0;

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
            {noAlerts ? (
                <div style={{ padding: 16, background: 'rgba(16,185,129,0.06)', border: '1px dashed rgba(16,185,129,0.3)', borderRadius: 8, color: '#059669', fontSize: 13, fontWeight: 600, textAlign: 'center' }}>
                     No major structural alerts or toxicity flags identified.
                </div>
            ) : (
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: 8 }}>
                    {/* CSV Toxicity Flags */}
                    {csvTox.map(t => <AlertChip key={t} type="tox">Known {t}</AlertChip>)}

                    {/* PAINS and Reactive Groups (from SMILES) */}
                    {alerts.map(a => <AlertChip key={a} type="pains">{a}</AlertChip>)}

                    {/* CYP and hERG Estimations (Phase 2 preview) */}
                    {cypPredictors.map(c => <AlertChip key={c} type="warning">{c}</AlertChip>)}
                    {hergPredictors.map(h => <AlertChip key={h} type="warning">{h}</AlertChip>)}
                </div>
            )}

            <div style={{ fontSize: 10, color: '#94A3B8', marginTop: 8 }}>
                * Alerts are rule-based estimations using localized SMILES pattern matching. They are intended for triage, not as confident endpoints.
            </div>
        </div>
    );
}
