import React from 'react';
import { computePhysChem } from '../utils/scoring';

const RuleRow = ({ label, value, pass, thresholdLabel }) => (
    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '8px 0', borderBottom: '1px solid rgba(0,0,0,0.04)' }}>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
            <span style={{ fontSize: 13, fontWeight: 600, color: '#334155' }}>{label}</span>
            <span style={{ fontSize: 10, color: '#94A3B8' }}>{thresholdLabel}</span>
        </div>
        <div style={{ display: 'flex', alignItems: 'center', gap: 10 }}>
            <span style={{ fontSize: 13, fontFamily: "'Space Mono',monospace", color: '#475569', fontWeight: 600 }}>
                {value !== null && value !== undefined ? (typeof value === 'number' ? value.toFixed(2).replace(/\.00$/, '') : value) : 'N/A'}
            </span>
            {pass === true && <span style={{ color: '#10B981', display: 'flex', alignItems: 'center', justifyContent: 'center', width: 20, height: 20, background: 'rgba(16,185,129,0.1)', borderRadius: '50%', fontSize: 12 }}></span>}
            {pass === false && <span style={{ color: '#EF4444', display: 'flex', alignItems: 'center', justifyContent: 'center', width: 20, height: 20, background: 'rgba(239,68,68,0.1)', borderRadius: '50%', fontSize: 12 }}></span>}
            {pass === null && <span style={{ color: '#94A3B8', display: 'flex', alignItems: 'center', justifyContent: 'center', width: 20, height: 20, background: 'rgba(148,163,184,0.1)', borderRadius: '50%', fontSize: 12 }}>-</span>}
        </div>
    </div>
);

export default function LipinskiPanel({ mol, smiles }) {
    if (!mol) return null;

    const { rules, lipinskiPass, lipinskiFailures, veberPass, heavyAtoms, estimatedMW } = computePhysChem(mol);
    const { lipinski, veber } = rules;

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
            {/* Lipinski Rule of 5 */}
            <div style={{ background: '#F8FAFC', borderRadius: 10, padding: 16, border: '1px solid rgba(0,0,0,0.05)' }}>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                    <div style={{ fontSize: 13, fontWeight: 700, color: '#1E293B' }}>Lipinski Rule of 5</div>
                    {lipinskiPass ?
                        <span style={{ background: '#10B981', color: 'white', padding: '2px 8px', borderRadius: 4, fontSize: 10, fontWeight: 700 }}>PASS</span> :
                        <span style={{ background: '#EF4444', color: 'white', padding: '2px 8px', borderRadius: 4, fontSize: 10, fontWeight: 700 }}>FAIL ({lipinskiFailures} violations)</span>
                    }
                </div>

                <RuleRow label="Molecular Weight" value={lipinski.mw.val} pass={lipinski.mw.pass} thresholdLabel=" 500 Da" />
                <RuleRow label="LogP" value={lipinski.logp.val} pass={lipinski.logp.pass} thresholdLabel=" 5" />
                <RuleRow label="H-Bond Donors" value={lipinski.hbd.val} pass={lipinski.hbd.pass} thresholdLabel=" 5" />
                <RuleRow label="H-Bond Acceptors" value={lipinski.hba.val} pass={lipinski.hba.pass} thresholdLabel=" 10" />
            </div>

            {/* Veber Rules */}
            <div style={{ background: '#F8FAFC', borderRadius: 10, padding: 16, border: '1px solid rgba(0,0,0,0.05)' }}>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                    <div style={{ fontSize: 13, fontWeight: 700, color: '#1E293B' }}>Veber Rules</div>
                    {veberPass ?
                        <span style={{ background: '#10B981', color: 'white', padding: '2px 8px', borderRadius: 4, fontSize: 10, fontWeight: 700 }}>PASS</span> :
                        <span style={{ background: '#EF4444', color: 'white', padding: '2px 8px', borderRadius: 4, fontSize: 10, fontWeight: 700 }}>FAIL</span>
                    }
                </div>

                <RuleRow label="Rotatable Bonds" value={veber.rotbonds.val} pass={veber.rotbonds.pass} thresholdLabel=" 10" />
                <RuleRow label="Polar Surface Area" value={veber.tpsa.val} pass={veber.tpsa.pass} thresholdLabel=" 140 " />
            </div>

            {/* Estimated Props Note */}
            {(lipinski.mw.val === estimatedMW && heavyAtoms > 0) && (
                <div style={{ fontSize: 10, color: '#64748B', display: 'flex', gap: 6 }}>
                    <span></span> <span>MW was missing from CSV and estimated from SMILES ({heavyAtoms} heavy atoms).</span>
                </div>
            )}
        </div>
    );
}
