//  Structural Alerts, PAINS, CYP & hERG (Rule-based heuristics) 
// NOTE: These are pattern-based estimations on SMILES strings, NOT ML models.
// Use for triage only. Always confirm with validated assays.

/** PAINS patterns  common pan-assay interference structures */
const PAINS_PATTERNS = [
    { name: "Quinone", regex: /[oO]=c1cc[oO]cc1|O=C1C=CC\(=O\)C=C1/, type: "PAINS" },
    { name: "Catechol", regex: /Oc1ccc\(O\)|c1cc\(O\)c\(O\)cc1/, type: "PAINS" },
    { name: "Rhodanine", regex: /O=C1CSC\(=S\)N1|SC\(=S\)/, type: "PAINS" },
    { name: "Enol/Enamine", regex: /C\(=C\)O|C\(=C\)N/, type: "PAINS" },
    { name: "Thiol", regex: /\[SH\]|CS(?!N|O)|c[sS](?!n)/, type: "PAINS" },
    { name: "Imine", regex: /C=N(?!O)/, type: "PAINS" },
    { name: "Phenothiazine", regex: /c1csc2ccccc2n1|c1cc2cccc[nS]c2cc1/, type: "PAINS" },
    { name: "Azo", regex: /N=Nc|c N=N/, type: "PAINS" },
];

/** Reactive group alerts */
const REACTIVE_PATTERNS = [
    { name: "Michael Acceptor", regex: /C=CC=O|C=CC\(=O\)/, type: "Reactive" },
    { name: "Aldehyde", regex: /\[CH\]=O|C\(=O\)\[H\]|CHO/, type: "Reactive" },
    { name: "Acyl Halide", regex: /C\(=O\)Cl|C\(=O\)Br|C\(=O\)F/, type: "Reactive" },
    { name: "Epoxide", regex: /C1OC1|[Cc]1o[Cc]1/, type: "Reactive" },
    { name: "Isocyanate", regex: /N=C=O/, type: "Reactive" },
    { name: "Isothiocyanate", regex: /N=C=S/, type: "Reactive" },
    { name: "Peroxide", regex: /OO(?![^)]*\))/, type: "Reactive" },
    { name: "Hydrazine", regex: /N-?N(?!O|=)/, type: "Reactive" },
    { name: "-Lactam", regex: /C1\(=O\)NCC1|C1\(=O\)CCN1/, type: "Reactive" },
    { name: "Disulfide", regex: /SS/, type: "Reactive" },
    { name: "Halo-carbonyl", regex: /C\(=O\)[ClBrFI]/, type: "Reactive" },
    { name: "Heavy Metal Ligand", regex: /\[As|Se\]|\[Hg\]|\[Pb\]/, type: "Toxicophore" },
    { name: "Nitro", regex: /\[N\+\]\(=O\)\[O-\]|\[N\+\].*?=O/, type: "Toxicophore" },
    { name: "Polyene", regex: /C=CC=CC=C/, type: "Reactive" },
];

/** Aggregator patterns */
const AGGREGATOR_PATTERNS = [
    { name: "Long Aliphatic Chain", regex: /CCCCCC(?!c)/, type: "Aggregator" },
    { name: "Basic Amphiphile", regex: /N.{0,10}CCCCC/, type: "Aggregator" },
];

export function detectStructuralAlerts(smiles) {
    if (!smiles) return [];
    const alerts = [];
    const allPatterns = [...PAINS_PATTERNS, ...REACTIVE_PATTERNS, ...AGGREGATOR_PATTERNS];
    for (const p of allPatterns) {
        if (p.regex.test(smiles)) alerts.push({ name: p.name, type: p.type });
    }
    return alerts;
}

/** CYP isoform risk (heuristic, rule-based) */
export function predictCYPFlags(smiles) {
    if (!smiles) return [];
    const flags = [];
    const hasBasicN = /\[NH\]|\[NH2\]|\[NH3\]|N\)/.test(smiles);
    const aromaticCount = (smiles.match(/c/g) || []).length;
    const hasFuran = /c1ccoc1|c1occc1/.test(smiles);
    const hasPiperidine = /N1CCCCC1|N1CCCC1/.test(smiles);
    const mw = smiles.length > 5 ? smiles.length * 4.5 : 0; // rough proxy

    if (hasBasicN && aromaticCount > 6) {
        flags.push({ isoform: "CYP2D6", risk: "Possible substrate", reason: "Basic N + multi-aromatic" });
    }
    if (aromaticCount > 10 || mw > 500) {
        flags.push({ isoform: "CYP3A4", risk: "Possible substrate", reason: "Large / multi-ring scaffold" });
    }
    if (/[Nn].*c1ccc/.test(smiles) || /c1ccc.*[Nn]/.test(smiles)) {
        flags.push({ isoform: "CYP1A2", risk: "Possible substrate", reason: "Aromatic amine detected" });
    }
    if (hasFuran) {
        flags.push({ isoform: "CYP3A4", risk: "Reactive metabolite risk", reason: "Furan ring (potential epoxide)" });
    }
    if (hasPiperidine && aromaticCount > 4) {
        flags.push({ isoform: "CYP2D6", risk: "Possible inhibitor", reason: "Piperidine + aromatic" });
    }
    return flags;
}

/** hERG liability risk (heuristic) */
export function predicthERG(smiles) {
    if (!smiles) return { risk: "Unknown", color: "#94A3B8", reason: "" };
    const hasBasicN = /\[NH\]|\[NH2\]|\[NH3\]|\bN\b/.test(smiles);
    const aromaticCount = (smiles.match(/c/g) || []).length;
    const hasLargeAromatic = aromaticCount > 8;
    const hasPiperidine = /N1CCCCC1/.test(smiles);
    const hasBenzene = /c1ccccc1/.test(smiles);

    if ((hasBasicN || hasPiperidine) && aromaticCount > 8) {
        return { risk: "High", color: "#EF4444", reason: "Basic N + large aromatic  classic hERG pharmacophore" };
    }
    if ((hasBasicN && hasBenzene) || (hasPiperidine && aromaticCount > 4)) {
        return { risk: "Moderate", color: "#F59E0B", reason: "Partial hERG pharmacophore (basic N + aromatic)" };
    }
    if (hasLargeAromatic) {
        return { risk: "Low-Moderate", color: "#F59E0B", reason: "Large aromatic system  monitor" };
    }
    return { risk: "Low", color: "#00C896", reason: "No obvious hERG pharmacophore detected" };
}

/** BBB permeability estimate */
export function predictBBB(mol, smiles) {
    // Based on Clark's rule: MW < 400, LogP 1-3, HBD < 3, HBA < 7, TPSA < 90
    const mw = parseFloat(mol['MW'] || mol['Molecular Weight'] || 0);
    const logp = parseFloat(mol['LogP'] || 0);
    const tpsa = parseFloat(mol['TPSA'] || 0);
    const hbd = parseFloat(mol['HBD'] || 0);

    if (!mw && !logp) return { prediction: "Unknown", color: "#94A3B8", confidence: "Low" };

    let score = 0;
    if (mw > 0 && mw < 400) score++;
    if (logp >= 1 && logp <= 3) score++;
    if (tpsa > 0 && tpsa < 90) score++;
    if (hbd < 3) score++;

    if (score >= 3) return { prediction: "CNS+ (likely)", color: "#00C896", confidence: "Moderate" };
    if (score === 2) return { prediction: "CNS (uncertain)", color: "#F59E0B", confidence: "Low" };
    return { prediction: "CNS (unlikely)", color: "#EF4444", confidence: "Moderate" };
}

/** Lipinski Rule of 5 */
export function checkLipinski(mol, smiles) {
    const mw = parseFloat(mol['MW'] || mol['Molecular Weight'] || 0);
    const logp = parseFloat(mol['LogP'] || 0);
    const hbd = parseFloat(mol['HBD'] || 0);
    const hba = parseFloat(mol['HBA'] || 0);

    return [
        { rule: "MW  500", value: mw || null, pass: mw > 0 ? mw <= 500 : null, unit: "Da" },
        { rule: "LogP  5", value: logp || null, pass: mol['LogP'] ? logp <= 5 : null, unit: "" },
        { rule: "HBD  5", value: hbd || null, pass: mol['HBD'] ? hbd <= 5 : null, unit: "" },
        { rule: "HBA  10", value: hba || null, pass: mol['HBA'] ? hba <= 10 : null, unit: "" },
    ];
}

/** Veber Rules */
export function checkVeber(mol) {
    const rot = parseFloat(mol['RotBonds'] || mol['Rotatable bonds'] || 0);
    const tpsa = parseFloat(mol['TPSA'] || 0);
    return [
        { rule: "RotBonds  10", value: rot || null, pass: mol['RotBonds'] ? rot <= 10 : null, unit: "" },
        { rule: "TPSA  140 ", value: tpsa || null, pass: mol['TPSA'] ? tpsa <= 140 : null, unit: "" },
    ];
}

/** Ghose Filter */
export function checkGhose(mol) {
    const mw = parseFloat(mol['MW'] || mol['Molecular Weight'] || 0);
    const logp = parseFloat(mol['LogP'] || 0);
    return [
        { rule: "MW 160480", value: mw || null, pass: mw > 0 ? (mw >= 160 && mw <= 480) : null, unit: "Da" },
        { rule: "LogP 0.45.6", value: logp || null, pass: mol['LogP'] ? (logp >= -0.4 && logp <= 5.6) : null, unit: "" },
    ];
}

/** Ligand Efficiency */
export function computeLE(pIC50, heavyAtoms) {
    if (!pIC50 || !heavyAtoms || heavyAtoms === 0) return null;
    return parseFloat((pIC50 / heavyAtoms).toFixed(3));
}

/** Binding Efficiency Index */
export function computeBEI(pIC50, mw) {
    if (!pIC50 || !mw || mw === 0) return null;
    return parseFloat((pIC50 / (mw / 1000)).toFixed(2));
}

/** Lipophilic Efficiency (LipE = pIC50 - LogP) */
export function computeLipE(pIC50, logp) {
    if (pIC50 === null || pIC50 === undefined || logp === null || logp === undefined) return null;
    return parseFloat((pIC50 - logp).toFixed(2));
}
