import { useEffect, useRef, useId } from "react";

/**
 * Renders a 2D molecule structure from a SMILES string using smiles-drawer.
 * Falls back to a styled placeholder if SMILES is invalid or missing.
 */
export default function MoleculeStructure({ smiles, width = 320, height = 260, theme = "light" }) {
    const canvasRef = useRef(null);
    const canvasId = "smiles-canvas-" + useId().replace(/:/g, "");

    useEffect(() => {
        if (!canvasRef.current) return;
        const canvas = canvasRef.current;
        const ctx = canvas.getContext("2d");

        if (!smiles || smiles.trim() === "") {
            ctx.clearRect(0, 0, width, height);
            ctx.fillStyle = "#F1F5F9";
            ctx.fillRect(0, 0, width, height);
            ctx.fillStyle = "#94A3B8";
            ctx.font = "13px sans-serif";
            ctx.textAlign = "center";
            ctx.fillText("No SMILES available", width / 2, height / 2);
            return;
        }

        try {
            // smiles-drawer 2.1.7 API
            const SD = window.SmilesDrawer || require("smiles-drawer");

            // Use the main wrapper to construct options
            const options = {
                width: parseInt(width, 10),
                height: parseInt(height, 10),
                bondThickness: 1.4,
                fontSizeLarge: 8,
                fontSizeSmall: 6,
                compactDrawing: false,
                themes: {
                    light: {
                        C: "#1E293B", O: "#DC2626", N: "#1D4ED8",
                        S: "#D97706", F: "#059669", Cl: "#059669",
                        Br: "#92400E", I: "#6D28D9", P: "#D97706",
                        BACKGROUND: "#FFFFFF",
                    },
                },
            };

            // In v2, SmiDrawer unified class
            const SmiDrawer = SD.SmiDrawer || SD.default?.SmiDrawer;
            if (SmiDrawer) {
                const drawer = new SmiDrawer(options);
                // draw accepts (smiles, target element selector, theme, success cb, error cb)
                // We MUST pass a string selector, otherwise it tries to querySelectorAll('[object HTMLCanvasElement]')
                drawer.draw(
                    smiles.trim(),
                    `#${canvasId}`,
                    theme,
                    () => { },
                    (err) => {
                        console.error("SMILES parse/draw error:", err);
                        fallbackRender(ctx, width, height, smiles);
                    }
                );
            } else {
                fallbackRender(ctx, width, height, smiles);
            }
        } catch (err) {
            console.error("Structure renderer error:", err);
            fallbackRender(ctx, width, height, smiles);
        }
    }, [smiles, width, height, theme, canvasId]);

    const fallbackRender = (ctx, w, h, s) => {
        ctx.clearRect(0, 0, w, h);
        ctx.fillStyle = "#FEF2F2";
        ctx.fillRect(0, 0, w, h);
        ctx.fillStyle = "#EF4444";
        ctx.font = "12px sans-serif";
        ctx.textAlign = "center";
        ctx.fillText("Could not render structure", w / 2, h / 2 - 10);
        ctx.fillStyle = "#94A3B8";
        ctx.font = "10px monospace";
        ctx.fillText(s.slice(0, 40) + (s.length > 40 ? "..." : ""), w / 2, h / 2 + 12);
    };

    return (
        <canvas
            id={canvasId}
            ref={canvasRef}
            width={width}
            height={height}
            style={{
                borderRadius: 12,
                border: "1px solid rgba(0,0,0,0.07)",
                background: "#ffffff",
                maxWidth: "100%",
                display: "block",
                width: `${width}px`,
                height: `${height}px`
            }}
        />
    );
}
