/**
 * Helper utilities for working with SMILES strings directly.
 * Because we don't use a heavy chemoinformatics backend (like RDKit),
 * these are lightweight estimations.
 */

/**
 * Basic heuristic to estimate heavy atom count from a SMILES string.
 * This removes brackets, stereochemical symbols, numbers, bonds, etc.
 * and counts expected atomic symbols.
 */
export function countHeavyAtoms(smiles) {
    if (!smiles) return 0;

    // Remove brackets, stereochem, numbers, bonds to count heavy atoms
    const cleanSmiles = smiles.replace(/[\[\]@\+\-\.\\=\#\$/\d\(\)]/g, "");

    let cCount = 0;
    for (let i = 0; i < cleanSmiles.length; i++) {
        const char = cleanSmiles[i];
        const nextChar = cleanSmiles[i + 1] || "";
        const atom2 = char + nextChar;

        if (["Cl", "Br", "Si", "Se"].includes(atom2)) {
            cCount++; i++;
        } else if (["C", "c", "N", "n", "O", "o", "S", "s", "F", "P", "I"].includes(char)) {
            cCount++;
        }
    }
    return cCount;
}

/**
 * Basic heuristic to estimate Molecular Weight (MW) from a SMILES string.
 */
export function estimateMW(smiles) {
    if (!smiles) return 0;

    const cleanSmiles = smiles.replace(/[\[\]@\+\-\.\\=\#\$/\d\(\)]/g, "");

    let heavyAtoms = 0;
    let estimatedMW = 0;

    for (let i = 0; i < cleanSmiles.length; i++) {
        const char = cleanSmiles[i];
        const nextChar = cleanSmiles[i + 1] || "";
        const atom2 = char + nextChar;

        if (["Cl", "Br", "Si", "Se"].includes(atom2)) {
            heavyAtoms++;
            if (atom2 === "Cl") estimatedMW += 35.5;
            else if (atom2 === "Br") estimatedMW += 79.9;
            else if (atom2 === "Si") estimatedMW += 28.1;
            else if (atom2 === "Se") estimatedMW += 79.0;
            i++;
        } else if (["C", "c", "N", "n", "O", "o", "S", "s", "F", "P", "I"].includes(char)) {
            heavyAtoms++;
            if (char.toLowerCase() === "c") estimatedMW += 12.0;
            else if (char.toLowerCase() === "n") estimatedMW += 14.0;
            else if (char.toLowerCase() === "o") estimatedMW += 16.0;
            else if (char.toLowerCase() === "s") estimatedMW += 32.1;
            else if (char === "F") estimatedMW += 19.0;
            else if (char === "P") estimatedMW += 31.0;
            else if (char === "I") estimatedMW += 126.9;
        }
    }

    // Add roughly 1 H for each heavy atom as a generic baseline
    estimatedMW += heavyAtoms * 1.0;

    return parseFloat(estimatedMW.toFixed(2));
}
