import { useMemo } from "react";
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Cell } from "recharts";
import { computePCA, detectActivityCliffs } from "../utils/pca";
import { TIER_CONFIG } from "../utils/scoring";

const DESCRIPTOR_KEYS = ["Predicted pIC50", "Docking score", "LogP", "QED", "HIA", "MW", "TPSA", "LogS", "Fsp3"];

const Card = ({ children, style }) => (
    <div style={{ background: "#fff", border: "1px solid rgba(0,0,0,0.07)", borderRadius: 14, padding: 20, boxShadow: "0 2px 8px rgba(0,0,0,0.05)", ...style }}>
        {children}
    </div>
);
const CardTitle = ({ children }) => (
    <div style={{ fontSize: 10, fontWeight: 700, color: "#64748B", letterSpacing: 1.5, textTransform: "uppercase", marginBottom: 14 }}>{children}</div>
);

export default function SpaceTab({ molecules, onSelectMol, setActiveTab }) {
    const { pcaPoints, cliffs } = useMemo(() => {
        if (molecules.length < 3) return { pcaPoints: [], cliffs: [] };
        const matrix = molecules.map(m =>
            DESCRIPTOR_KEYS.map(k => parseFloat(m[k]) || 0)
        );
        const projections = computePCA(matrix);
        const pcaPoints = molecules.map((m, i) => ({
            pc1: projections[i]?.pc1 || 0,
            pc2: projections[i]?.pc2 || 0,
            name: m["Molecule name"] || m["Molecule S.no"] || `${i + 1}`,
            tier: m._tier,
            score: m._score,
            color: TIER_CONFIG[m._tier]?.color || "#888",
            mol: m,
        }));
        const cliffs = detectActivityCliffs(molecules);
        return { pcaPoints, cliffs };
    }, [molecules]);

    const CustomTip = ({ active, payload }) => {
        if (!active || !payload?.length) return null;
        const d = payload[0]?.payload;
        return (
            <div style={{ background: "#fff", border: `1px solid ${d?.color}66`, borderRadius: 10, padding: "10px 14px", fontSize: 12, boxShadow: "0 8px 24px rgba(0,0,0,0.12)" }}>
                <div style={{ color: d?.color, fontWeight: 700, marginBottom: 4, fontFamily: "'Space Mono',monospace" }}>{d?.name}</div>
                <div style={{ color: "#64748B" }}>PC1: <strong style={{ color: "#1E293B" }}>{d?.pc1?.toFixed(2)}</strong></div>
                <div style={{ color: "#64748B" }}>PC2: <strong style={{ color: "#1E293B" }}>{d?.pc2?.toFixed(2)}</strong></div>
                <div style={{ color: "#64748B" }}>MPO: <strong style={{ color: "#1E293B" }}>{d?.score}/13</strong></div>
                <div style={{ marginTop: 4 }}>
                    <span style={{ background: TIER_CONFIG[d?.tier]?.bg, border: `1px solid ${TIER_CONFIG[d?.tier]?.border}`, color: TIER_CONFIG[d?.tier]?.text, borderRadius: 5, padding: "1px 6px", fontSize: 10, fontWeight: 700 }}>
                        {d?.tier}
                    </span>
                </div>
            </div>
        );
    };

    if (molecules.length < 3) return (
        <Card style={{ textAlign: "center", padding: 60 }}>
            <div style={{ fontSize: 36, marginBottom: 12 }}></div>
            <div style={{ color: "#64748B" }}>Upload a CSV with at least 3 molecules to see the chemical space map.</div>
        </Card>
    );

    return (
        <div style={{ display: "flex", flexDirection: "column", gap: 20 }}>
            {/* PCA Scatter */}
            <Card>
                <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", marginBottom: 12 }}>
                    <CardTitle>Chemical Space (PCA)  {pcaPoints.length} Molecules</CardTitle>
                    <div style={{ display: "flex", gap: 12 }}>
                        {Object.entries(TIER_CONFIG).map(([tier, cfg]) => (
                            <div key={tier} style={{ display: "flex", alignItems: "center", gap: 5, fontSize: 11 }}>
                                <span style={{ width: 9, height: 9, borderRadius: "50%", background: cfg.color, display: "inline-block" }} />
                                <span style={{ color: "#64748B" }}>{tier}</span>
                            </div>
                        ))}
                    </div>
                </div>
                <ResponsiveContainer width="100%" height={440}>
                    <ScatterChart margin={{ top: 10, right: 20, bottom: 30, left: 10 }}>
                        <CartesianGrid strokeDasharray="3 3" stroke="rgba(0,0,0,0.06)" />
                        <XAxis dataKey="pc1" name="PC1" type="number"
                            label={{ value: "Principal Component 1", position: "bottom", fill: "#64748B", fontSize: 11 }}
                            tick={{ fill: "#64748B", fontSize: 10 }} axisLine={false} tickLine={false} />
                        <YAxis dataKey="pc2" name="PC2" type="number"
                            label={{ value: "Principal Component 2", angle: -90, position: "insideLeft", fill: "#64748B", fontSize: 11 }}
                            tick={{ fill: "#64748B", fontSize: 10 }} axisLine={false} tickLine={false} />
                        <Tooltip content={<CustomTip />} />
                        <Scatter data={pcaPoints} onClick={d => { onSelectMol(d.mol); setActiveTab("detail"); }}>
                            {pcaPoints.map((p, i) => <Cell key={i} fill={p.color} opacity={0.85} cursor="pointer" />)}
                        </Scatter>
                    </ScatterChart>
                </ResponsiveContainer>
                <div style={{ fontSize: 11, color: "#64748B", textAlign: "center", marginTop: 4 }}>
                    PCA computed from: {DESCRIPTOR_KEYS.join(", ")}. Click a point to view the molecule.
                </div>
            </Card>

            {/* Activity Cliffs */}
            <Card>
                <CardTitle>Activity Cliffs  Similar Molecules with Large Activity Differences</CardTitle>
                {cliffs.length === 0 ? (
                    <div style={{ color: "#64748B", fontSize: 13 }}>No significant activity cliffs detected (threshold: similarity &gt; 0.85, pIC50 &gt; 1.0).</div>
                ) : (
                    <div style={{ overflowX: "auto" }}>
                        <table style={{ width: "100%", borderCollapse: "collapse", fontSize: 12 }}>
                            <thead>
                                <tr>
                                    {["Molecule A", "Molecule B", "Similarity", "pIC50", "Risk"].map(h => (
                                        <th key={h} style={{ padding: "8px 12px", fontSize: 10, fontWeight: 700, color: "#64748B", textTransform: "uppercase", letterSpacing: 1, borderBottom: "1px solid rgba(0,0,0,0.07)", background: "#F8FAFC", whiteSpace: "nowrap" }}>{h}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {cliffs.map((c, i) => (
                                    <tr key={i} style={{ background: i % 2 === 0 ? "#F8FAFC" : "#fff" }}>
                                        <td style={{ padding: "8px 12px", fontFamily: "'Space Mono',monospace", fontSize: 11, color: "#1E293B" }}>{c.mol1}</td>
                                        <td style={{ padding: "8px 12px", fontFamily: "'Space Mono',monospace", fontSize: 11, color: "#1E293B" }}>{c.mol2}</td>
                                        <td style={{ padding: "8px 12px", textAlign: "center" }}>{(c.similarity * 100).toFixed(0)}%</td>
                                        <td style={{ padding: "8px 12px", textAlign: "center", fontWeight: 700, color: "#EF4444" }}>{c.deltaPIC50}</td>
                                        <td style={{ padding: "8px 12px" }}>
                                            <span style={{ background: "rgba(239,68,68,0.1)", border: "1px solid rgba(239,68,68,0.3)", color: "#DC2626", borderRadius: 5, padding: "2px 7px", fontSize: 10, fontWeight: 700 }}> Cliff</span>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                )}
            </Card>
        </div>
    );
}
