//  PCA (Principal Component Analysis) 
// Pure JS implementation using power iteration for eigenvectors.

function mean(arr) { return arr.reduce((s, v) => s + v, 0) / arr.length; }

function stddev(arr, m) {
    const avg = m !== undefined ? m : mean(arr);
    return Math.sqrt(arr.reduce((s, v) => s + (v - avg) ** 2, 0) / arr.length) || 1;
}

/** Standardize columns (z-score) */
function standardize(matrix) {
    const n = matrix.length;
    if (n === 0) return { data: [], means: [], stds: [] };
    const m = matrix[0].length;
    const means = Array.from({ length: m }, (_, j) => mean(matrix.map(r => r[j])));
    const stds = Array.from({ length: m }, (_, j) => stddev(matrix.map(r => r[j]), means[j]));
    const data = matrix.map(row => row.map((v, j) => (v - means[j]) / stds[j]));
    return { data, means, stds };
}

/** Matrix multiply vector */
function matVecMul(A, v) {
    return A.map(row => row.reduce((s, a, j) => s + a * v[j], 0));
}

/** Normalize vector */
function normalize(v) {
    const norm = Math.sqrt(v.reduce((s, x) => s + x * x, 0)) || 1;
    return v.map(x => x / norm);
}

/** Power iteration to find dominant eigenvector */
function powerIteration(A, iterations = 150) {
    const m = A.length;
    let v = normalize(Array.from({ length: m }, () => Math.random() - 0.5));
    for (let i = 0; i < iterations; i++) {
        const Av = matVecMul(A, v);
        v = normalize(Av);
    }
    // Eigenvalue (Rayleigh quotient)
    const Av = matVecMul(A, v);
    const eigenvalue = v.reduce((s, vi, i) => s + vi * Av[i], 0);
    return { vector: v, eigenvalue };
}

/** Deflate covariance matrix by one eigenpair */
function deflate(C, eigVec, eigVal) {
    const m = C.length;
    return C.map((row, i) =>
        row.map((val, j) => val - eigVal * eigVec[i] * eigVec[j])
    );
}

/** Covariance matrix */
function covMatrix(data) {
    const n = data.length;
    const m = data[0].length;
    const C = Array.from({ length: m }, () => Array(m).fill(0));
    for (const row of data) {
        for (let i = 0; i < m; i++) {
            for (let j = 0; j < m; j++) {
                C[i][j] += row[i] * row[j] / (n - 1);
            }
        }
    }
    return C;
}

/**
 * Compute 2D PCA projection.
 * @param {number[][]} matrix - rows = samples, cols = features
 * @returns {{ pc1: number, pc2: number }[]}
 */
export function computePCA(matrix) {
    if (!matrix || matrix.length < 3) return matrix.map(() => ({ pc1: 0, pc2: 0 }));

    const { data } = standardize(matrix);
    const C = covMatrix(data);

    // First principal component
    const { vector: v1, eigenvalue: e1 } = powerIteration(C);
    // Second principal component (on deflated matrix)
    const C2 = deflate(C, v1, e1);
    const { vector: v2 } = powerIteration(C2);

    return data.map(row => ({
        pc1: row.reduce((s, val, j) => s + val * v1[j], 0),
        pc2: row.reduce((s, val, j) => s + val * v2[j], 0),
    }));
}

/**
 * Detect activity cliffs.
 * @param {object[]} molecules
 * @param {number} simThreshold  - descriptor similarity (Euclidean, normalized)
 * @param {number} actDiff       - minimum |pIC50| to be a cliff
 */
export function detectActivityCliffs(molecules, simThreshold = 0.15, actDiff = 1.0) {
    const KEYS = ["Predicted pIC50", "Docking score", "LogP", "QED", "HIA"];
    const cliffs = [];

    const vecs = molecules.map(m => KEYS.map(k => parseFloat(m[k]) || 0));
    // Normalize
    const mins = KEYS.map((_, j) => Math.min(...vecs.map(v => v[j])));
    const maxs = KEYS.map((_, j) => Math.max(...vecs.map(v => v[j])));
    const normed = vecs.map(v => v.map((val, j) =>
        (maxs[j] - mins[j]) > 0 ? (val - mins[j]) / (maxs[j] - mins[j]) : 0
    ));

    for (let i = 0; i < molecules.length; i++) {
        for (let j = i + 1; j < molecules.length; j++) {
            const dist = Math.sqrt(normed[i].reduce((s, v, k) => s + (v - normed[j][k]) ** 2, 0));
            if (dist < simThreshold) {
                const actI = parseFloat(molecules[i]["Predicted pIC50"]) || 0;
                const actJ = parseFloat(molecules[j]["Predicted pIC50"]) || 0;
                if (Math.abs(actI - actJ) >= actDiff) {
                    cliffs.push({
                        mol1: molecules[i]["Molecule name"] || molecules[i]["Molecule S.no"] || `${i}`,
                        mol2: molecules[j]["Molecule name"] || molecules[j]["Molecule S.no"] || `${j}`,
                        similarity: parseFloat((1 - dist).toFixed(3)),
                        deltaPIC50: parseFloat(Math.abs(actI - actJ).toFixed(2)),
                    });
                }
            }
        }
    }
    return cliffs.slice(0, 20); // top 20
}
