//  Scoring Engine 

export const DEFAULT_WEIGHTS = {
    pIC50: 1, docking: 1, logp: 1, logs: 1,
    qed: 1, caco: 1, hia: 1, pains: 1, tox: 1,
};

export const TIER_CONFIG = {
    "Tier 1": { label: "Strong Hit", color: "#00C896", bg: "rgba(0,200,150,0.12)", border: "#00C896", text: "#00C896" },
    "Tier 2": { label: "Promising", color: "#F59E0B", bg: "rgba(245,158,11,0.12)", border: "#F59E0B", text: "#F59E0B" },
    "Tier 3": { label: "Conditional", color: "#F97316", bg: "rgba(249,115,22,0.12)", border: "#F97316", text: "#F97316" },
    "Tier 4": { label: "Deprioritize", color: "#EF4444", bg: "rgba(239,68,68,0.12)", border: "#EF4444", text: "#EF4444" },
};

export const PARAM_COLORS = ["#00C896", "#0891B2", "#F59E0B", "#F97316", "#A855F7", "#EC4899", "#34D399", "#60A5FA"];

function get(row, k) {
    const val = row[k];
    if (val === undefined || val === null || val === "") return null;
    const n = parseFloat(val);
    return isNaN(n) ? null : n;
}

export function scoreMolecule(row, weights = DEFAULT_WEIGHTS) {
    const w = { ...DEFAULT_WEIGHTS, ...weights };
    let score = 0;
    let maxScore = 0;
    const flags = [];

    // pIC50 (max 2 * weight)
    const pic50 = get(row, "Predicted pIC50");
    maxScore += 2 * w.pIC50;
    if (pic50 !== null) {
        if (pic50 >= 6.5) score += 2 * w.pIC50;
        else if (pic50 >= 6.0) score += 1 * w.pIC50;
    }

    // Docking score (max 2 * weight)
    const dock = get(row, "Docking score");
    maxScore += 2 * w.docking;
    if (dock !== null) {
        if (dock <= -7.0) score += 2 * w.docking;
        else if (dock <= -6.0) score += 1 * w.docking;
    }

    // LogP (max 1 * weight)
    const logp = get(row, "LogP");
    maxScore += 1 * w.logp;
    if (logp !== null && logp >= 0 && logp <= 5) score += 1 * w.logp;

    // LogS (max 1 * weight)
    const logs = get(row, "LogS");
    maxScore += 1 * w.logs;
    if (logs !== null && logs > -4) score += 1 * w.logs;

    // QED (max 1 * weight)
    const qed = get(row, "QED");
    maxScore += 1 * w.qed;
    if (qed !== null && qed >= 0.5) score += 1 * w.qed;

    // Caco-2R (max 1 * weight)
    const caco = get(row, "Caco 2R");
    maxScore += 1 * w.caco;
    if (caco !== null && caco > -5.15) score += 1 * w.caco;

    // HIA (max 1 * weight)
    const hia = get(row, "HIA");
    maxScore += 1 * w.hia;
    if (hia !== null && hia >= 80) score += 1 * w.hia;

    // PAINS (max 1 * weight)
    const pains = get(row, "PAINS");
    maxScore += 1 * w.pains;
    if (pains !== null && pains === 0) score += 1 * w.pains;

    // Toxicity flags (max 2 * weight)
    const toxCols = ["Hepatotoxicity", "Mutagenicity", "Carcinogenicity", "Cardiotoxicity", "Nephrotoxicity"];
    maxScore += 2 * w.tox;
    const cleanCount = toxCols.filter(c => { const v = get(row, c); return v !== null && v === 0; }).length;
    if (cleanCount === 5) score += 2 * w.tox;
    else if (cleanCount >= 3) score += 1 * w.tox;

    // Collect toxicity flags
    const flagLabels = {
        Mutagenicity: "Mutagen", Hepatotoxicity: "Hepatotox",
        Carcinogenicity: "Carcino", Cardiotoxicity: "Cardiotox", Nephrotoxicity: "Nephrotox"
    };
    Object.entries(flagLabels).forEach(([col, label]) => {
        const v = get(row, col);
        if (v !== null && v === 1) flags.push(label);
    });
    if (pains !== null && pains === 1) flags.push("PAINS");

    // Normalize score to 013 equivalent for compatibility
    const normalizedScore = maxScore > 0 ? Math.round((score / maxScore) * 13 * 10) / 10 : 0;

    let tier;
    const pct = maxScore > 0 ? score / maxScore : 0;
    if (pct >= 0.77 && flags.length === 0) tier = "Tier 1";
    else if (pct >= 0.62) tier = "Tier 2";
    else if (pct >= 0.46) tier = "Tier 3";
    else tier = "Tier 4";

    return { score: normalizedScore, rawScore: score, maxScore, tier, flags };
}

export function parseCSV(text) {
    const lines = text.trim().split(/\r?\n/);
    const headers = lines[0].split(",").map(h => h.trim().replace(/^\uFEFF/, ""));
    return lines.slice(1).filter(l => l.trim()).map(line => {
        const values = [];
        let cur = "", inQ = false;
        for (let i = 0; i < line.length; i++) {
            if (line[i] === '"') { inQ = !inQ; continue; }
            if (line[i] === "," && !inQ) { values.push(cur); cur = ""; continue; }
            cur += line[i];
        }
        values.push(cur);
        const obj = {};
        headers.forEach((h, i) => { obj[h] = values[i]?.trim() ?? ""; });
        return obj;
    });
}

//  Property Calculators 

/**
 * Basic heuristic to estimate heavy atom count and MW from SMILES string.
 * This is a rough estimation used when CSV columns are missing.
 */
function estimateFromSMILES(smiles) {
    if (!smiles) return { heavyAtoms: 0, estimatedMW: 0 };

    // Remove brackets, stereochem, numbers, bonds to count heavy atoms
    const cleanSmiles = smiles.replace(/[\[\]@\+\-\.\\=\#\$/\d\(\)]/g, "");

    let heavyAtoms = 0;
    let estimatedMW = 0;

    for (let i = 0; i < cleanSmiles.length; i++) {
        const char = cleanSmiles[i];
        const nextChar = cleanSmiles[i + 1] || "";
        const atom2 = char + nextChar;

        if (["Cl", "Br", "Si", "Se"].includes(atom2)) {
            heavyAtoms++;
            if (atom2 === "Cl") estimatedMW += 35.5;
            else if (atom2 === "Br") estimatedMW += 79.9;
            else if (atom2 === "Si") estimatedMW += 28.1;
            else if (atom2 === "Se") estimatedMW += 79.0;
            i++; // skip next char
        } else if (["C", "c", "N", "n", "O", "o", "S", "s", "F", "P", "I"].includes(char)) {
            heavyAtoms++;
            if (char.toLowerCase() === "c") estimatedMW += 12.0;
            else if (char.toLowerCase() === "n") estimatedMW += 14.0;
            else if (char.toLowerCase() === "o") estimatedMW += 16.0;
            else if (char.toLowerCase() === "s") estimatedMW += 32.1;
            else if (char === "F") estimatedMW += 19.0;
            else if (char === "P") estimatedMW += 31.0;
            else if (char === "I") estimatedMW += 126.9;
        }
    }

    // Add roughly 1 H for each heavy atom as a generic baseline
    estimatedMW += heavyAtoms * 1.0;

    return { heavyAtoms, estimatedMW };
}

/**
 * Checks Lipinski and Veber rules for a molecule.
 * Expected columns: MW, LogP, HBA, HBD, RotBonds, TPSA
 */
export function computePhysChem(row) {
    const smiles = row["SMILES"] || row["Smiles"] || row["smiles"];
    const est = estimateFromSMILES(smiles);

    const mw = get(row, "MW") || get(row, "Molecular Weight") || est.estimatedMW;
    const logp = get(row, "LogP");
    const hba = get(row, "HBA") || get(row, "H-Bond Acceptors");
    const hbd = get(row, "HBD") || get(row, "H-Bond Donors");
    const rotbonds = get(row, "RotBonds") || get(row, "Rotatable Bonds");
    const tpsa = get(row, "TPSA");

    let lipinskiFailures = 0;
    const rules = {
        lipinski: {
            mw: { val: mw, pass: mw === null ? null : mw <= 500 },
            logp: { val: logp, pass: logp === null ? null : logp <= 5 },
            hbd: { val: hbd, pass: hbd === null ? null : hbd <= 5 },
            hba: { val: hba, pass: hba === null ? null : hba <= 10 }
        },
        veber: {
            rotbonds: { val: rotbonds, pass: rotbonds === null ? null : rotbonds <= 10 },
            tpsa: { val: tpsa, pass: tpsa === null ? null : tpsa <= 140 }
        }
    };

    if (rules.lipinski.mw.pass === false) lipinskiFailures++;
    if (rules.lipinski.logp.pass === false) lipinskiFailures++;
    if (rules.lipinski.hbd.pass === false) lipinskiFailures++;
    if (rules.lipinski.hba.pass === false) lipinskiFailures++;

    const lipinskiPass = lipinskiFailures <= 1; // RO5 allows 1 violation
    const veberPass = (rules.veber.rotbonds.pass !== false) && (rules.veber.tpsa.pass !== false);

    return { rules, lipinskiPass, lipinskiFailures, veberPass, heavyAtoms: est.heavyAtoms, estimatedMW: est.estimatedMW };
}
