import { useState, useCallback, useMemo, useRef } from "react";
import {
  BarChart, Bar, ScatterChart, Scatter, XAxis, YAxis, CartesianGrid,
  Tooltip, ResponsiveContainer, Cell,
} from "recharts";
import { scoreMolecule, parseCSV, TIER_CONFIG, PARAM_COLORS, DEFAULT_WEIGHTS } from "./utils/scoring";
import { countHeavyAtoms, estimateMW } from "./utils/smilesUtils";
import { computeLE, computeBEI, computeLipE } from "./utils/structuralAlerts";
import { exportCSV, exportSDF } from "./utils/exportUtils";
import DetailTab from "./tabs/DetailTab";
import SpaceTab from "./tabs/SpaceTab";
import CompareTab from "./tabs/CompareTab";
import SettingsModal from "./components/SettingsModal";

//  Shared mini-components 
const Card = ({ children, style }) => (
  <div style={{ background: "#fff", border: "1px solid rgba(0,0,0,0.07)", borderRadius: 14, padding: 20, boxShadow: "0 2px 8px rgba(0,0,0,0.05)", ...style }}>{children}</div>
);
const CardTitle = ({ children }) => (
  <div style={{ fontSize: 10, fontWeight: 700, color: "#64748B", letterSpacing: 1.5, textTransform: "uppercase", marginBottom: 14 }}>{children}</div>
);
const StatCard = ({ label, value, sub, color = "#0891B2" }) => (
  <div style={{ background: "#fff", border: `1px solid ${color}33`, borderRadius: 12, padding: "14px 18px", flex: "1 1 130px", boxShadow: "0 2px 8px rgba(0,0,0,0.05)", position: "relative", overflow: "hidden" }}>
    <div style={{ position: "absolute", top: 0, left: 0, right: 0, height: 2, background: `linear-gradient(90deg,transparent,${color},transparent)` }} />
    <div style={{ fontSize: 26, fontWeight: 800, color, fontFamily: "'Space Mono',monospace" }}>{value}</div>
    <div style={{ fontSize: 10, color: "#64748B", marginTop: 2, fontWeight: 600, letterSpacing: 1, textTransform: "uppercase" }}>{label}</div>
    {sub && <div style={{ fontSize: 10, color: "#94A3B8", marginTop: 1 }}>{sub}</div>}
  </div>
);
const TierBadge = ({ tier }) => {
  const c = TIER_CONFIG[tier] || TIER_CONFIG["Tier 4"];
  return <span style={{ background: c.bg, border: `1px solid ${c.border}`, color: c.text, borderRadius: 6, padding: "2px 8px", fontSize: 11, fontWeight: 700, whiteSpace: "nowrap", fontFamily: "'Space Mono',monospace" }}>{tier}  {c.label}</span>;
};
const CustomTooltip = ({ active, payload, label }) => {
  if (!active || !payload?.length) return null;
  return (
    <div style={{ background: "#fff", border: "1px solid rgba(8,145,178,0.3)", borderRadius: 8, padding: "9px 13px", fontSize: 12, color: "#1E293B", boxShadow: "0 8px 24px rgba(0,0,0,0.1)" }}>
      {label && <div style={{ color: "#0891B2", fontWeight: 700, marginBottom: 3 }}>{label}</div>}
      {payload.map((p, i) => <div key={i} style={{ color: p.color || "#1E293B" }}>{p.name}: <strong>{typeof p.value === "number" && p.value.toFixed ? p.value.toFixed(2) : p.value}</strong></div>)}
    </div>
  );
};

//  Main App 
export default function App() {
  const [molecules, setMolecules] = useState([]);
  const [headers, setHeaders] = useState([]);
  const [fileName, setFileName] = useState("");
  const [dragging, setDragging] = useState(false);
  const [activeTab, setActiveTab] = useState("overview");
  const [search, setSearch] = useState("");
  const [tierFilter, setTierFilter] = useState("All");
  const [selectedMol, setSelectedMol] = useState(null);
  const [sortCol, setSortCol] = useState("score");
  const [sortDir, setSortDir] = useState("desc");
  const [weights, setWeights] = useState({ ...DEFAULT_WEIGHTS });
  const [showSettings, setShowSettings] = useState(false);
  const [compareSet, setCompareSet] = useState(new Set());
  const [scaffoldGroup, setScaffoldGroup] = useState(false);
  const fileRef = useRef();

  //  Enrich molecule with computed fields 
  const enrichMol = useCallback((row, w) => {
    const { score, tier, flags } = scoreMolecule(row, w);
    const smiles = row["SMILES"] || row["Smiles"] || row["smiles"] || "";
    const ha = countHeavyAtoms(smiles) || (parseFloat(row["HeavyAtoms"]) || 0);
    const mw = parseFloat(row["MW"] || row["Molecular Weight"]) || estimateMW(smiles) || 0;
    const pic50 = parseFloat(row["Predicted pIC50"]) || null;
    const logp = parseFloat(row["LogP"]) || null;
    return {
      ...row,
      _score: score,
      _tier: tier,
      _flags: flags,
      _le: computeLE(pic50, ha),
      _bei: computeBEI(pic50, mw),
      _lipe: computeLipE(pic50, logp),
      _smilesExtracted: smiles, // Cache for easier access in UI
    };
  }, []);

  const processFile = useCallback((file) => {
    if (!file) return;
    setFileName(file.name);
    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const rows = parseCSV(e.target.result);
        const hdrs = Object.keys(rows[0] || {});
        setHeaders(hdrs);
        setMolecules(rows.map(r => enrichMol(r, weights)));
        setSelectedMol(null);
        setActiveTab("overview");
        setCompareSet(new Set());
      } catch { alert("Could not parse CSV. Please check the format."); }
    };
    reader.readAsText(file);
  }, [enrichMol, weights]);

  // Re-score when weights change
  const applyWeights = useCallback((newW) => {
    setWeights(newW);
    setMolecules(prev => prev.map(m => enrichMol(m, newW)));
  }, [enrichMol]);

  const onDrop = useCallback((e) => {
    e.preventDefault(); setDragging(false);
    const f = e.dataTransfer.files[0];
    if (f?.name.endsWith(".csv")) processFile(f);
  }, [processFile]);

  //  Stats 
  const stats = useMemo(() => {
    if (!molecules.length) return null;
    const t = (n) => molecules.filter(m => m._tier === n).length;
    const best = molecules.reduce((a, b) => a._score > b._score ? a : b, molecules[0]);
    return {
      total: molecules.length, t1: t("Tier 1"), t2: t("Tier 2"), t3: t("Tier 3"), t4: t("Tier 4"),
      flagged: molecules.filter(m => m._flags.length > 0).length,
      avgScore: (molecules.reduce((s, m) => s + m._score, 0) / molecules.length).toFixed(1),
      best,
    };
  }, [molecules]);

  const tierDist = useMemo(() => !stats ? [] : [
    { name: "Tier 1", count: stats.t1, color: TIER_CONFIG["Tier 1"].color },
    { name: "Tier 2", count: stats.t2, color: TIER_CONFIG["Tier 2"].color },
    { name: "Tier 3", count: stats.t3, color: TIER_CONFIG["Tier 3"].color },
    { name: "Tier 4", count: stats.t4, color: TIER_CONFIG["Tier 4"].color },
  ], [stats]);

  const mpoBar = useMemo(() =>
    [...molecules].sort((a, b) => b._score - a._score).slice(0, 20).map(m => ({
      name: (m["Molecule name"] || m["Molecule S.no"] || "?").substring(0, 12),
      score: m._score, color: TIER_CONFIG[m._tier]?.color || "#888",
    })), [molecules]);

  const scatterData = useMemo(() => molecules.map(m => ({
    x: parseFloat(m["Predicted pIC50"]) || 0,
    y: parseFloat(m["Docking score"]) || 0,
    name: m["Molecule name"] || m["Molecule S.no"] || "?",
    tier: m._tier, score: m._score, color: TIER_CONFIG[m._tier]?.color || "#888", mol: m,
  })), [molecules]);

  const tableData = useMemo(() => {
    let data = molecules.filter(m => {
      const name = (m["Molecule name"] || m["Molecule S.no"] || "").toLowerCase();
      const q = search.toLowerCase();
      return (!q || name.includes(q) || (m._smilesExtracted || "").toLowerCase().includes(q)) &&
        (tierFilter === "All" || m._tier === tierFilter);
    });
    data = [...data].sort((a, b) => {
      const map = { score: "_score", pIC50: "Predicted pIC50", dock: "Docking score", logp: "LogP", hia: "HIA", le: "_le" };
      const k = map[sortCol] || "_score";
      const av = parseFloat(a[k]) || 0, bv = parseFloat(b[k]) || 0;
      return sortDir === "desc" ? bv - av : av - bv;
    });
    return data;
  }, [molecules, search, tierFilter, sortCol, sortDir]);

  const toggleSort = (col) => {
    if (sortCol === col) setSortDir(d => d === "desc" ? "asc" : "desc");
    else { setSortCol(col); setSortDir("desc"); }
  };

  const toggleCompare = (key) => {
    setCompareSet(prev => {
      const s = new Set(prev);
      if (s.has(key)) s.delete(key); else if (s.size < 5) s.add(key);
      return s;
    });
  };

  //  Upload screen 
  if (!molecules.length) {
    return (
      <div style={{ minHeight: "100vh", background: "#F1F5F9", fontFamily: "'DM Sans',system-ui,sans-serif", display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center" }}>
        <div style={{ position: "fixed", inset: 0, backgroundImage: "linear-gradient(rgba(8,145,178,0.06) 1px,transparent 1px),linear-gradient(90deg,rgba(8,145,178,0.06) 1px,transparent 1px)", backgroundSize: "40px 40px" }} />
        <div style={{ position: "relative", textAlign: "center", maxWidth: 560, padding: "0 24px" }}>
          <div style={{ fontSize: 11, color: "#0891B2", letterSpacing: 3, textTransform: "uppercase", marginBottom: 14, fontWeight: 700 }}> In Silico Analysis Platform</div>
          <h1 style={{ fontSize: 42, fontWeight: 900, lineHeight: 1.1, marginBottom: 12, fontFamily: "'Space Mono',monospace", background: "linear-gradient(135deg,#1E293B 0%,#0891B2 50%,#00C896 100%)", WebkitBackgroundClip: "text", WebkitTextFillColor: "transparent" }}>
            Hit Identification<br />Dashboard
          </h1>
          <p style={{ color: "#64748B", marginBottom: 36, lineHeight: 1.7, fontSize: 14 }}>
            Upload any CSV with molecular data for MPO scoring, ADMET analysis, PCA chemical space, drug-likeness rules, and interactive visualizations.
          </p>
          <div onDrop={onDrop} onDragOver={e => { e.preventDefault(); setDragging(true); }} onDragLeave={() => setDragging(false)} onClick={() => fileRef.current?.click()}
            style={{ border: `2px dashed ${dragging ? "#0891B2" : "rgba(8,145,178,0.3)"}`, borderRadius: 20, padding: "44px 36px", cursor: "pointer", background: dragging ? "rgba(8,145,178,0.05)" : "rgba(255,255,255,0.8)", transition: "all 0.3s", boxShadow: "0 4px 20px rgba(0,0,0,0.06)" }}>
            <div style={{ fontSize: 44, marginBottom: 10 }}></div>
            <div style={{ fontWeight: 700, fontSize: 16, color: "#1E293B", marginBottom: 5 }}>Drop your CSV file here</div>
            <div style={{ color: "#64748B", fontSize: 13 }}>or click to browse</div>
            <div style={{ marginTop: 18, display: "inline-flex", alignItems: "center", gap: 8, background: "linear-gradient(135deg,#0891B2,#00C896)", color: "#fff", borderRadius: 10, padding: "9px 22px", fontWeight: 700, fontSize: 13 }}>Upload CSV</div>
          </div>
          <input ref={fileRef} type="file" accept=".csv" onChange={e => processFile(e.target.files[0])} style={{ display: "none" }} />
          <div style={{ marginTop: 24, display: "flex", gap: 10, justifyContent: "center", flexWrap: "wrap" }}>
            {["Predicted pIC50", "Docking score", "LogP", "QED", "HIA", "SMILES", "Mutagenicity"].map(p => (
              <span key={p} style={{ fontSize: 10, color: "#0891B2", background: "rgba(8,145,178,0.08)", border: "1px solid rgba(8,145,178,0.2)", borderRadius: 5, padding: "3px 9px", fontFamily: "'Space Mono',monospace" }}>{p}</span>
            ))}
          </div>
        </div>
        <style>{`@import url('https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&family=DM+Sans:wght@400;500;600;700;800;900&display=swap');*{box-sizing:border-box;margin:0;padding:0;}button{transition:all 0.2s;}button:hover{opacity:0.85;}`}</style>
      </div>
    );
  }

  //  Tab styles 
  const tabStyle = (t) => ({
    background: activeTab === t ? "rgba(8,145,178,0.1)" : "#fff",
    border: `1px solid ${activeTab === t ? "#0891B2" : "rgba(0,0,0,0.12)"}`,
    color: activeTab === t ? "#0891B2" : "#64748B",
    borderRadius: 8, padding: "6px 14px", cursor: "pointer", fontSize: 12, fontWeight: 600, letterSpacing: 0.4, transition: "all 0.2s",
  });

  const TABS = ["overview", "space", "scatter", "table", "compare", "detail"];

  return (
    <div style={{ minHeight: "100vh", background: "#F1F5F9", fontFamily: "'DM Sans',system-ui,sans-serif", color: "#1E293B" }}>
      <div style={{ position: "fixed", inset: 0, zIndex: 0, backgroundImage: "linear-gradient(rgba(8,145,178,0.04) 1px,transparent 1px),linear-gradient(90deg,rgba(8,145,178,0.04) 1px,transparent 1px)", backgroundSize: "40px 40px", pointerEvents: "none" }} />

      {/* Header */}
      <div style={{ background: "rgba(255,255,255,0.95)", backdropFilter: "blur(20px)", borderBottom: "1px solid rgba(0,0,0,0.08)", padding: "12px 28px", display: "flex", alignItems: "center", gap: 14, position: "sticky", top: 0, zIndex: 100, boxShadow: "0 1px 8px rgba(0,0,0,0.06)" }}>
        <div style={{ fontSize: 19, fontWeight: 900, fontFamily: "'Space Mono',monospace", background: "linear-gradient(135deg,#0891B2,#00C896)", WebkitBackgroundClip: "text", WebkitTextFillColor: "transparent", letterSpacing: -1 }}> HitID</div>
        <span style={{ fontSize: 10, color: "#0891B2", background: "rgba(8,145,178,0.08)", border: "1px solid rgba(8,145,178,0.2)", borderRadius: 4, padding: "2px 7px", letterSpacing: 1, textTransform: "uppercase" }}>In Silico</span>
        <span style={{ fontSize: 12, color: "#0891B2" }}>{fileName}</span>
        <span style={{ fontSize: 11, color: "#64748B" }}>{stats.total} molecules</span>
        <div style={{ flex: 1 }} />
        <div style={{ display: "flex", gap: 5, flexWrap: "wrap" }}>
          {TABS.map(t => (
            <button key={t} style={tabStyle(t)} onClick={() => setActiveTab(t)}>
              {t.charAt(0).toUpperCase() + t.slice(1)}
              {t === "compare" && compareSet.size > 0 ? ` (${compareSet.size})` : ""}
            </button>
          ))}
        </div>
        <button onClick={() => setShowSettings(true)} style={{ background: "rgba(8,145,178,0.08)", border: "1px solid rgba(8,145,178,0.25)", color: "#0891B2", borderRadius: 8, padding: "6px 12px", cursor: "pointer", fontSize: 13 }} title="MPO Settings"></button>
        <button onClick={() => exportCSV(molecules, headers)} style={{ background: "rgba(0,200,150,0.08)", border: "1px solid rgba(0,200,150,0.3)", color: "#00A876", borderRadius: 8, padding: "6px 12px", cursor: "pointer", fontSize: 12, fontWeight: 600 }}> CSV</button>
        <button onClick={() => exportSDF(molecules, headers)} style={{ background: "rgba(0,200,150,0.08)", border: "1px solid rgba(0,200,150,0.3)", color: "#00A876", borderRadius: 8, padding: "6px 12px", cursor: "pointer", fontSize: 12, fontWeight: 600 }}> SDF</button>
        <button onClick={() => { setMolecules([]); setFileName(""); setHeaders([]); setSelectedMol(null); fileRef.current?.click(); }}
          style={{ background: "rgba(8,145,178,0.08)", border: "1px solid rgba(8,145,178,0.3)", color: "#0891B2", borderRadius: 8, padding: "6px 12px", cursor: "pointer", fontSize: 12, fontWeight: 600 }}> New</button>
        <input ref={fileRef} type="file" accept=".csv" onChange={e => processFile(e.target.files[0])} style={{ display: "none" }} />
      </div>

      <div style={{ position: "relative", zIndex: 1, padding: "22px 26px", maxWidth: 1640, margin: "0 auto" }}>
        {/* Stats row */}
        <div style={{ display: "flex", gap: 10, marginBottom: 22, flexWrap: "wrap" }}>
          <StatCard label="Total Screened" value={stats.total} color="#0891B2" />
          <StatCard label="Tier 1 Hits" value={stats.t1} sub="Strong Hits" color="#00C896" />
          <StatCard label="Tier 2" value={stats.t2} sub="Promising" color="#F59E0B" />
          <StatCard label="Conditional" value={stats.t3} color="#F97316" />
          <StatCard label="Deprioritize" value={stats.t4} color="#EF4444" />
          <StatCard label="Safety Flagged" value={stats.flagged} color="#A855F7" />
          <StatCard label="Avg MPO" value={stats.avgScore} sub="/13" color="#3B82F6" />
          <StatCard label="Best MPO" value={`${stats.best?._score}/13`} sub={stats.best?.["Molecule name"] || stats.best?.["Molecule S.no"] || ""} color="#00C896" />
        </div>

        {/*  OVERVIEW  */}
        {activeTab === "overview" && (
          <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 20 }}>
            <Card>
              <CardTitle>Hit Tier Distribution</CardTitle>
              <ResponsiveContainer width="100%" height={230}>
                <BarChart data={tierDist} barSize={48}>
                  <CartesianGrid strokeDasharray="3 3" stroke="rgba(0,0,0,0.06)" />
                  <XAxis dataKey="name" tick={{ fill: "#64748B", fontSize: 12 }} axisLine={false} tickLine={false} />
                  <YAxis tick={{ fill: "#64748B", fontSize: 11 }} axisLine={false} tickLine={false} />
                  <Tooltip content={<CustomTooltip />} />
                  <Bar dataKey="count" radius={[6, 6, 0, 0]}>{tierDist.map((e, i) => <Cell key={i} fill={e.color} />)}</Bar>
                </BarChart>
              </ResponsiveContainer>
            </Card>
            <Card>
              <CardTitle>Top 20  MPO Ranking</CardTitle>
              <ResponsiveContainer width="100%" height={230}>
                <BarChart data={mpoBar} layout="vertical" barSize={9} margin={{ left: 10, right: 20 }}>
                  <CartesianGrid strokeDasharray="3 3" stroke="rgba(0,0,0,0.06)" horizontal={false} />
                  <XAxis type="number" domain={[0, 13]} tick={{ fill: "#64748B", fontSize: 10 }} axisLine={false} tickLine={false} />
                  <YAxis type="category" dataKey="name" tick={{ fill: "#475569", fontSize: 10 }} width={78} axisLine={false} tickLine={false} />
                  <Tooltip content={<CustomTooltip />} />
                  <Bar dataKey="score" radius={[0, 4, 4, 0]}>{mpoBar.map((e, i) => <Cell key={i} fill={e.color} />)}</Bar>
                </BarChart>
              </ResponsiveContainer>
            </Card>
            <Card style={{ gridColumn: "1/-1" }}>
              <CardTitle>MPO Scoring Criteria  Parameter Thresholds</CardTitle>
              <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
                {[["Predicted pIC50", "6.5=2pt / 6.0=1pt", "#0891B2"], ["Docking Score", "7.0=2pt / 6.0=1pt", "#0891B2"], ["LogP", "05 = 1pt", "#00C896"], ["LogS", "> 4 = 1pt", "#00C896"], ["QED", "0.5 = 1pt", "#F59E0B"], ["Caco-2R", "> 5.15 = 1pt", "#F59E0B"], ["HIA", "80% = 1pt", "#F97316"], ["PAINS", "=0 = 1pt", "#EF4444"], ["Tox Flags", "All 5 clean = 2pt", "#A855F7"], ["Max Score", "13 Points Total", "#1E293B"]].map(([p, r, c]) => (
                  <div key={p} style={{ background: `${c}0D`, border: `1px solid ${c}33`, borderRadius: 10, padding: "9px 12px", flex: "1 1 150px" }}>
                    <div style={{ fontSize: 11, fontWeight: 700, color: c, fontFamily: "'Space Mono',monospace", marginBottom: 3 }}>{p}</div>
                    <div style={{ fontSize: 10, color: "#64748B" }}>{r}</div>
                  </div>
                ))}
              </div>
            </Card>
          </div>
        )}

        {/*  SPACE (PCA)  */}
        {activeTab === "space" && (
          <SpaceTab molecules={molecules} onSelectMol={setSelectedMol} setActiveTab={setActiveTab} />
        )}

        {/*  SCATTER  */}
        {activeTab === "scatter" && (
          <Card>
            <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", marginBottom: 14 }}>
              <CardTitle>pIC50 vs Docking Score  Chemical Space</CardTitle>
              <div style={{ display: "flex", gap: 12 }}>
                {Object.entries(TIER_CONFIG).map(([t, c]) => (
                  <div key={t} style={{ display: "flex", alignItems: "center", gap: 5, fontSize: 11 }}>
                    <span style={{ width: 9, height: 9, borderRadius: "50%", background: c.color, display: "inline-block" }} />
                    <span style={{ color: "#64748B" }}>{t}</span>
                  </div>
                ))}
              </div>
            </div>
            <ResponsiveContainer width="100%" height={450}>
              <ScatterChart margin={{ top: 10, right: 20, bottom: 30, left: 10 }}>
                <CartesianGrid strokeDasharray="3 3" stroke="rgba(0,0,0,0.06)" />
                <XAxis dataKey="x" name="pIC50" type="number"
                  label={{ value: "Predicted pIC50", position: "bottom", fill: "#64748B", fontSize: 12 }}
                  tick={{ fill: "#64748B", fontSize: 11 }} axisLine={false} tickLine={false} />
                <YAxis dataKey="y" name="Docking"
                  label={{ value: "Docking Score", angle: -90, position: "insideLeft", fill: "#64748B", fontSize: 12 }}
                  tick={{ fill: "#64748B", fontSize: 11 }} axisLine={false} tickLine={false} />
                <Tooltip content={({ active, payload }) => {
                  if (!active || !payload?.length) return null;
                  const d = payload[0]?.payload;
                  return <div style={{ background: "#fff", border: `1px solid ${d?.color}66`, borderRadius: 10, padding: "10px 14px", fontSize: 12, boxShadow: "0 8px 24px rgba(0,0,0,0.1)" }}>
                    <div style={{ color: d?.color, fontWeight: 700, marginBottom: 4, fontFamily: "'Space Mono',monospace" }}>{d?.name}</div>
                    <div style={{ color: "#64748B" }}>pIC50: <strong style={{ color: "#1E293B" }}>{d?.x?.toFixed(2)}</strong></div>
                    <div style={{ color: "#64748B" }}>Docking: <strong style={{ color: "#1E293B" }}>{d?.y?.toFixed(3)}</strong></div>
                    <div style={{ color: "#64748B" }}>MPO: <strong style={{ color: "#1E293B" }}>{d?.score}/13</strong></div>
                    <div style={{ marginTop: 5 }}><TierBadge tier={d?.tier} /></div>
                  </div>;
                }} />
                <Scatter data={scatterData} onClick={d => { setSelectedMol(d.mol); setActiveTab("detail"); }}>
                  {scatterData.map((e, i) => <Cell key={i} fill={e.color} opacity={0.85} cursor="pointer" />)}
                </Scatter>
              </ScatterChart>
            </ResponsiveContainer>
            <div style={{ fontSize: 11, color: "#64748B", textAlign: "center", marginTop: 6 }}>Click any point to view the molecule's full profile</div>
          </Card>
        )}

        {/*  TABLE  */}
        {activeTab === "table" && (
          <div>
            <div style={{ display: "flex", gap: 10, marginBottom: 14, flexWrap: "wrap", alignItems: "center" }}>
              <input style={{ background: "#fff", border: "1px solid rgba(0,0,0,0.15)", borderRadius: 8, padding: "8px 13px", color: "#1E293B", fontSize: 13, outline: "none", maxWidth: 260, width: "100%" }}
                placeholder="  Search name or SMILES" value={search} onChange={e => setSearch(e.target.value)} />
              <div style={{ display: "flex", gap: 5 }}>
                {["All", "Tier 1", "Tier 2", "Tier 3", "Tier 4"].map(t => (
                  <button key={t} style={tabStyle(tierFilter === t ? t : "_")} onClick={() => setTierFilter(t)}>{t}</button>
                ))}
              </div>
              <label style={{ display: "flex", alignItems: "center", gap: 6, fontSize: 12, color: "#64748B", cursor: "pointer" }}>
                <input type="checkbox" checked={scaffoldGroup} onChange={e => setScaffoldGroup(e.target.checked)} />
                Group by scaffold
              </label>
              <div style={{ marginLeft: "auto", fontSize: 12, color: "#64748B" }}>{tableData.length}/{molecules.length} molecules</div>
              {compareSet.size > 0 && <button onClick={() => { setActiveTab("compare"); }} style={{ background: "rgba(8,145,178,0.08)", border: "1px solid rgba(8,145,178,0.3)", color: "#0891B2", borderRadius: 8, padding: "6px 12px", cursor: "pointer", fontSize: 12, fontWeight: 600 }}>Compare {compareSet.size} </button>}
            </div>
            <Card style={{ padding: 0, overflow: "hidden" }}>
              <div style={{ overflowX: "auto", overflowY: "auto", maxHeight: "65vh" }}>
                <table style={{ width: "100%", borderCollapse: "collapse" }}>
                  <thead>
                    <tr>
                      {[["", null], ["#", null], ["Name", null], ["SMILES", null], ["pIC50", "pIC50"], ["Docking", "dock"], ["LogP", "logp"], ["QED", null], ["HIA%", "hia"], ["LE", "le"], ["LipE", null], ["MPO", "score"], ["Tier", null], ["Flags", null], ["Details", null]].map(([label, key]) => (
                        <th key={label} onClick={() => key && toggleSort(key)}
                          style={{ padding: "9px 12px", fontSize: 10, fontWeight: 700, color: "#64748B", textTransform: "uppercase", letterSpacing: 1, borderBottom: "1px solid rgba(0,0,0,0.07)", cursor: key ? "pointer" : "default", userSelect: "none", whiteSpace: "nowrap", background: "#F8FAFC" }}>
                          {label}{key && sortCol === key ? (sortDir === "desc" ? " " : " ") : key ? " " : ""}
                        </th>
                      ))}
                    </tr>
                  </thead>
                  <tbody>
                    {tableData.map((mol, i) => {
                      const name = mol["Molecule name"] || mol["Molecule S.no"] || `Row ${i + 1}`;
                      const cfg = TIER_CONFIG[mol._tier] || {};
                      const molKey = mol["Molecule name"] || mol["Molecule S.no"] || "";
                      const isCompare = compareSet.has(molKey);
                      const td = { padding: "9px 12px", fontSize: 12, borderBottom: "1px solid rgba(0,0,0,0.05)", whiteSpace: "nowrap" };
                      return (
                        <tr key={i} style={{ background: isCompare ? "rgba(8,145,178,0.04)" : i % 2 === 0 ? "#F8FAFC" : "#fff" }}>
                          <td style={td}>
                            <input type="checkbox" checked={isCompare} onChange={() => toggleCompare(molKey)} />
                          </td>
                          <td style={{ ...td, color: "#94A3B8" }}>{i + 1}</td>
                          <td style={{ ...td, fontWeight: 700, color: "#1E293B", fontFamily: "'Space Mono',monospace", fontSize: 11 }}>{name}</td>
                          <td style={{ ...td, maxWidth: 180, overflow: "hidden", textOverflow: "ellipsis", color: "#64748B", fontFamily: "monospace", fontSize: 10 }}>{(mol._smilesExtracted || "").slice(0, 36)}{mol._smilesExtracted?.length > 36 ? "" : ""}</td>
                          <td style={{ ...td, color: "#0891B2", textAlign: "right" }}>{parseFloat(mol["Predicted pIC50"])?.toFixed(2) || ""}</td>
                          <td style={{ ...td, color: "#3B82F6", textAlign: "right" }}>{parseFloat(mol["Docking score"])?.toFixed(3) || ""}</td>
                          <td style={{ ...td, textAlign: "right" }}>{parseFloat(mol["LogP"])?.toFixed(2) || ""}</td>
                          <td style={{ ...td, textAlign: "right", color: parseFloat(mol["QED"]) >= 0.5 ? "#00C896" : "#EF4444" }}>{parseFloat(mol["QED"])?.toFixed(2) || ""}</td>
                          <td style={{ ...td, textAlign: "right", color: parseFloat(mol["HIA"]) >= 80 ? "#00C896" : "#F97316" }}>{parseFloat(mol["HIA"])?.toFixed(1) || ""}</td>
                          <td style={{ ...td, textAlign: "right", color: "#A855F7", fontFamily: "'Space Mono',monospace" }}>{mol._le || ""}</td>
                          <td style={{ ...td, textAlign: "right", color: "#EC4899" }}>{mol._lipe || ""}</td>
                          <td style={{ ...td, textAlign: "center" }}>
                            <span style={{ display: "inline-block", width: 30, height: 30, borderRadius: "50%", background: cfg.bg, border: `2px solid ${cfg.color}`, color: cfg.color, fontSize: 11, fontWeight: 900, lineHeight: "28px", textAlign: "center", fontFamily: "'Space Mono',monospace" }}>{mol._score}</span>
                          </td>
                          <td style={td}><TierBadge tier={mol._tier} /></td>
                          <td style={td}>
                            {mol._flags.length > 0
                              ? <div style={{ display: "flex", gap: 3, flexWrap: "wrap" }}>{mol._flags.map(f => <span key={f} style={{ fontSize: 9, background: "rgba(239,68,68,0.12)", border: "1px solid rgba(239,68,68,0.3)", color: "#EF4444", borderRadius: 4, padding: "1px 5px" }}>{f}</span>)}</div>
                              : <span style={{ color: "#00C896", fontSize: 11 }}> Clean</span>}
                          </td>
                          <td style={td}>
                            <button onClick={() => { setSelectedMol(mol); setActiveTab("detail"); }}
                              style={{ background: "rgba(8,145,178,0.08)", border: "1px solid rgba(8,145,178,0.3)", color: "#0891B2", borderRadius: 6, padding: "4px 9px", cursor: "pointer", fontSize: 11, fontWeight: 600 }}>View </button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </Card>
          </div>
        )}

        {/*  COMPARE  */}
        {activeTab === "compare" && (
          <CompareTab molecules={molecules} selected={compareSet} onToggleSelect={toggleCompare} onSelectMol={setSelectedMol} setActiveTab={setActiveTab} />
        )}

        {/*  DETAIL  */}
        {activeTab === "detail" && (
          <DetailTab mol={selectedMol} onClose={() => setSelectedMol(null)} setActiveTab={setActiveTab} />
        )}
      </div>

      {/* Settings modal */}
      {showSettings && <SettingsModal weights={weights} onChange={applyWeights} onClose={() => setShowSettings(false)} />}

      <style>{`
        @import url('https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&family=DM+Sans:wght@300;400;500;600;700;800;900&display=swap');
        *{box-sizing:border-box;margin:0;padding:0;}
        ::-webkit-scrollbar{width:6px;height:6px;}
        ::-webkit-scrollbar-track{background:rgba(0,0,0,0.04);}
        ::-webkit-scrollbar-thumb{background:rgba(8,145,178,0.4);border-radius:3px;}
        button{transition:all 0.2s;}button:hover{opacity:0.85;transform:translateY(-1px);}
      `}</style>
    </div>
  );
}