export function detectAlerts(smiles) {
    if (!smiles) return { alerts: [], cypPredictors: [], hergPredictors: [] };

    // Convert to uppercase for simpler basic matching where appropriate
    const s = smiles.toUpperCase();
    const alerts = [];

    // Reactive Groups (very basic heuristic SMILES patterns)
    if (s.includes("C(=O)C=C")) alerts.push("Michael Acceptor");
    if (s.includes("C1OC1")) alerts.push("Epoxide");
    if (s.includes("C(=O)CL") || s.includes("C(=O)BR")) alerts.push("Acyl Halide");
    if (s.includes("[CH]=O") || s.includes("C=O") && !s.includes("NC=O") && !s.includes("OC=O")) alerts.push("Aldehyde/Ketone");
    if (s.includes("C#N")) alerts.push("Nitrile");
    if (s.includes("N=C") || s.includes("C=N")) alerts.push("Imine");
    if (s.includes("NN") || s.includes("N-N")) alerts.push("Hydrazine/Azo");
    if (s.includes("S(=O)(=O)O") || s.includes("OS(=O)(=O)")) alerts.push("Sulfonate Ester");

    // PAINS / Aggregators (simplified)
    if (s.includes("C1=CC(=O)C=CC1=O")) alerts.push("Quinone");
    if (s.includes("C(=S)S") || s.includes("S=C")) alerts.push("Thiocarbonyl");
    if (s.includes("N2C(=O)CSC2=S")) alerts.push("Rhodanine");
    if (s.match(/C{8,}/)) alerts.push("Long Aliphatic Chain (>8C)");

    // CYP / hERG basic rule-based predictors (Phase 2 preview)
    const cypPredictors = [];
    if (s.match(/N([C,c]){2,3}/) && s.includes("C1=CC=CC=C1")) cypPredictors.push("Basic Amine + Aromatic (CYP2D6 liability)");
    if (s.match(/C{6,}/) && s.match(/C1=CC=CC=C1/)) cypPredictors.push("Highly Lipophilic Aromatic (CYP3A4 liability)");

    const hergPredictors = [];
    if (s.match(/N([C,c]){2,3}/) && (s.match(/C1=CC=CC=C1/g) || []).length > 1) hergPredictors.push("hERG Liability (Basic Amine + Multiple Aromatics)");

    return { alerts, cypPredictors, hergPredictors };
}
