import { useMemo } from "react";
import { RadarChart, Radar, PolarGrid, PolarAngleAxis, PolarRadiusAxis, ResponsiveContainer, Legend } from "recharts";
import { TIER_CONFIG, PARAM_COLORS } from "../utils/scoring";

const COMPARE_PARAMS = [
    ["pIC50", "Predicted pIC50"], ["Docking", "Docking score"], ["LogP", "LogP"],
    ["LogS", "LogS"], ["QED", "QED"], ["HIA (%)", "HIA"], ["Caco-2R", "Caco 2R"],
    ["PPB", "PPB"], ["MPO Score", "_score"],
];

const Card = ({ children, style }) => (
    <div style={{ background: "#fff", border: "1px solid rgba(0,0,0,0.07)", borderRadius: 14, padding: 20, boxShadow: "0 2px 8px rgba(0,0,0,0.05)", ...style }}>
        {children}
    </div>
);
const CardTitle = ({ children }) => (
    <div style={{ fontSize: 10, fontWeight: 700, color: "#64748B", letterSpacing: 1.5, textTransform: "uppercase", marginBottom: 14 }}>{children}</div>
);

export default function CompareTab({ molecules, selected, onToggleSelect, onSelectMol, setActiveTab }) {
    const compareMols = molecules.filter(m => selected.has(m["Molecule name"] || m["Molecule S.no"]));

    const radarData = useMemo(() => {
        const params = ["pIC50", "Docking", "QED", "HIA", "LogP", "MPO"];
        const norms = {
            pIC50: { lo: 4, hi: 9, key: "Predicted pIC50" },
            Docking: { lo: -12, hi: 0, key: "Docking score", inv: true },
            QED: { lo: 0, hi: 1, key: "QED" },
            HIA: { lo: 0, hi: 100, key: "HIA" },
            LogP: { lo: -2, hi: 7, key: "LogP" },
            MPO: { lo: 0, hi: 13, key: "_score" },
        };
        return params.map(p => {
            const cfg = norms[p];
            const row = { param: p };
            compareMols.forEach((m, i) => {
                let v = parseFloat(m[cfg.key]) || 0;
                if (cfg.inv) v = -v;
                row[`mol${i}`] = Math.max(0, Math.min(100, ((v - cfg.lo) / (cfg.hi - cfg.lo)) * 100));
            });
            return row;
        });
    }, [compareMols]);

    if (molecules.length === 0) return (
        <Card style={{ textAlign: "center", padding: 60 }}>
            <div style={{ fontSize: 36, marginBottom: 12 }}></div>
            <div style={{ color: "#64748B" }}>Upload a CSV first, then select molecules to compare.</div>
        </Card>
    );

    return (
        <div>
            {/* Molecule selector */}
            <Card style={{ marginBottom: 20 }}>
                <CardTitle>Select Molecules to Compare (up to 5)</CardTitle>
                <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
                    {molecules.slice(0, 40).map(m => {
                        const key = m["Molecule name"] || m["Molecule S.no"] || "";
                        const isSelected = selected.has(key);
                        const cfg = TIER_CONFIG[m._tier] || {};
                        return (
                            <button key={key} onClick={() => onToggleSelect(key)}
                                style={{ padding: "4px 12px", borderRadius: 7, border: `1px solid ${isSelected ? cfg.color : "rgba(0,0,0,0.12)"}`, background: isSelected ? cfg.bg : "#fff", color: isSelected ? cfg.text : "#64748B", fontSize: 11, fontWeight: isSelected ? 700 : 400, cursor: "pointer", fontFamily: "'Space Mono',monospace" }}>
                                {isSelected ? " " : ""}{key.slice(0, 16)}
                            </button>
                        );
                    })}
                </div>
                {molecules.length > 40 && <div style={{ fontSize: 11, color: "#94A3B8", marginTop: 8 }}>Showing first 40 molecules. Use Table search to find specific ones.</div>}
            </Card>

            {compareMols.length < 2 ? (
                <Card style={{ textAlign: "center", padding: 40 }}>
                    <div style={{ color: "#64748B", fontSize: 13 }}>Select at least 2 molecules above to compare them.</div>
                </Card>
            ) : (
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 20 }}>
                    {/* Radar overlay */}
                    <Card>
                        <CardTitle>Radar Overlay</CardTitle>
                        <ResponsiveContainer width="100%" height={280}>
                            <RadarChart data={radarData} outerRadius={90}>
                                <PolarGrid stroke="rgba(0,0,0,0.08)" />
                                <PolarAngleAxis dataKey="param" tick={{ fill: "#475569", fontSize: 11 }} />
                                <PolarRadiusAxis domain={[0, 100]} tick={{ fill: "#94A3B8", fontSize: 9 }} />
                                {compareMols.map((m, i) => (
                                    <Radar key={i} name={m["Molecule name"] || m["Molecule S.no"] || `Mol ${i + 1}`}
                                        dataKey={`mol${i}`} stroke={PARAM_COLORS[i]} fill={PARAM_COLORS[i]} fillOpacity={0.15} strokeWidth={2} />
                                ))}
                                <Legend />
                            </RadarChart>
                        </ResponsiveContainer>
                    </Card>

                    {/* Side-by-side table */}
                    <Card>
                        <CardTitle>Parameter Comparison</CardTitle>
                        <div style={{ overflowX: "auto" }}>
                            <table style={{ width: "100%", borderCollapse: "collapse", fontSize: 12 }}>
                                <thead>
                                    <tr>
                                        <th style={{ padding: "7px 10px", fontSize: 10, fontWeight: 700, color: "#64748B", textTransform: "uppercase", letterSpacing: 1, borderBottom: "1px solid rgba(0,0,0,0.07)", background: "#F8FAFC", textAlign: "left" }}>Parameter</th>
                                        {compareMols.map((m, i) => (
                                            <th key={i} style={{ padding: "7px 10px", fontSize: 10, fontWeight: 700, color: PARAM_COLORS[i], textTransform: "uppercase", letterSpacing: 1, borderBottom: "1px solid rgba(0,0,0,0.07)", background: "#F8FAFC", whiteSpace: "nowrap" }}>
                                                {(m["Molecule name"] || m["Molecule S.no"] || `Mol ${i + 1}`).slice(0, 14)}
                                            </th>
                                        ))}
                                    </tr>
                                </thead>
                                <tbody>
                                    {COMPARE_PARAMS.map(([label, key], ri) => (
                                        <tr key={label} style={{ background: ri % 2 === 0 ? "#F8FAFC" : "#fff" }}>
                                            <td style={{ padding: "7px 10px", color: "#475569", fontWeight: 500 }}>{label}</td>
                                            {compareMols.map((m, i) => {
                                                const v = m[key];
                                                const n = parseFloat(v);
                                                return (
                                                    <td key={i} style={{ padding: "7px 10px", textAlign: "center", fontFamily: "'Space Mono',monospace", color: PARAM_COLORS[i], fontWeight: 600 }}>
                                                        {v !== undefined && v !== null && v !== "" ? (isNaN(n) ? v : n.toFixed(2)) : ""}
                                                    </td>
                                                );
                                            })}
                                        </tr>
                                    ))}
                                    <tr style={{ background: "#F1F5F9" }}>
                                        <td style={{ padding: "7px 10px", fontWeight: 700, color: "#1E293B" }}>Tier</td>
                                        {compareMols.map((m, i) => {
                                            const cfg = TIER_CONFIG[m._tier] || {};
                                            return (
                                                <td key={i} style={{ padding: "7px 10px", textAlign: "center" }}>
                                                    <span style={{ background: cfg.bg, border: `1px solid ${cfg.border}`, color: cfg.text, borderRadius: 5, padding: "2px 7px", fontSize: 10, fontWeight: 700 }}>{m._tier}</span>
                                                </td>
                                            );
                                        })}
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </Card>
                </div>
            )}
        </div>
    );
}
